/**
 * 格式化日份（2021年9月17日）
 */
export function formatDateTime(datetime, type = "1") {
  var year = datetime.getFullYear().toString();
  var month = (datetime.getMonth() + 1).toString().padStart(2, "0");
  var date = datetime
    .getDate()
    .toString()
    .padStart(2, "0");
  var currentDate = "";
  if (type == "1") {
    // 格式化为日
    currentDate = year + "-" + month + "-" + date;
  } else if (type == "2") {
    // 格式化为月
    currentDate = year + "-" + month;
  } else if (type == "3") {
    // 格式化为年
    currentDate = year;
  }
  return currentDate;
}

/**
 * 计算前一天的函数
 * @param {*} obj
 * @returns
 */
export function getYesterday() {
  var time = new Date().getTime(); //当前的毫秒数
  var oneDay = 1000 * 60 * 60 * 24; //一天的毫秒数
  var yesterdayTime = time - oneDay; // 昨天的毫秒数
  var yesterday = new Date(yesterdayTime); // 昨天的中国标准时间
  return yesterday;
}


/**
 * 生成一个带参数的url
 */
export function createUrl(obj) {
  var length = obj && obj.length,
    idx = 0,
    url = obj.url + "?";
  for (var key in obj) {
    if (key != "url" && obj[key] !== null) {
      url += key + "=" + encodeURIComponent(obj[key]) + "&";
    }
  }
  var result = "";
  if (url.lastIndexOf("&") > -1) {
    result = url.substring(0, url.lastIndexOf("&"));
  }
  if (url.lastIndexOf("?") > -1) {
    result = url.substring(0, url.lastIndexOf("?"));
  }
  return result;
}

