<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <!-- 单选景区多选时间范围 start -->
			  
			  <el-form-item label="" prop="dateType">
			    <!-- 选择模式 start -->
			    <el-select
			      v-model="searchValidateForm.dateType"
			      filterable
			      placeholder="请选择模式"
			      clearable
			      @change="onChangeMode"
			    >
			      <el-option
			        v-for="(item, index) in searchModeOptions"
			        :key="index"
			        :label="item.label"
			        :value="item.value"
			      >
			      </el-option>
			    </el-select>
			    <!-- 选择模式 end -->
			  </el-form-item>
			  
            <!-- 输入起止时间 start -->
            <el-form-item label="" prop="startEndDate">
              <el-date-picker
                v-model="searchValidateForm.startEndDate"
                unlink-panels
                :type="dateType"
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
              >
              </el-date-picker>
            </el-form-item>
            <!-- 输入起止时间 end -->
			
            <el-form-item label="" prop="spotId">
              <!-- 选择景区名称 start -->
              <el-select
                v-model="searchValidateForm.spotId"
                filterable
                placeholder="请选择景区"
                clearable
              >
                <el-option
                  v-for="(item, index) in searchScenicOptions"
                  :key="index"
                  :label="item.SCENIC_SPOT_NAME"
                  :value="item.SCENIC_SPOT_ID"
                >
                </el-option>
              </el-select>
              <!-- 选择景区名称 end -->
            </el-form-item>
        </el-form>

        <!-- 搜索 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 搜索 end -->

        <!-- 刷新 start -->
        <el-button
          type="primary"
          icon="el-icon-refresh"
          @click="onRefresh('searchValidateForm')"
          >刷新</el-button
        >
        <!-- 刷新 end -->
      </el-header>
      <el-main>
        <!-- 显示柱状图 start -->
        <div class="my-echarts">
          <div id="myChart"></div>
        </div>
        <!-- 显示柱状图 end -->
      </el-main>
      <el-footer> </el-footer>
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'
import { formatDate,getYesterday,formatDateTime,sevenDate } from '../utils/utils.js'
export default {
  name: 'PartnerCompany',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      dateType: 'daterange', // 日期类型（月，日，年）
      valueFormat: 'yyyy-MM-dd', // 日期格式
      searchModeOptions: [
        {
          label: '按日查询',
          value: 1
        },
        {
          label: '按月查询',
          value: 2
        },
        {
          label: '按年查询',
          value: 3
        }
      ],

      searchScenicOptions: [],

      searchValidateForm: {
		dateType:1,
        spotId: '',
        // startEndDate: [sevenDate(), new Date()],
		startEndDate: [new Date(new Date()-604800000), new Date(new Date()-86400000)],
      }, // 搜索表单验证对象
      searchVF: {
        spotId: '',
        startEndDate: [],
      }, // 搜索需要提交的表单
      validateForm: {},
      total: 0,

      myChart: null, // echarts实例
      option: {}, // echarts option
      realTimeList: [], // 时间列表
      drysList: [], // 当日营收列表
      jxzList: [], // 当日在线订单列表

      // 搜索表单验证规则
      searchValidateFormRules: {},

      timer: null
    }
  },
  created() {
    // 接收缓存中的 realTimeLineSpotIdCopy
    // this.searchValidateForm.timeSpotId = JSON.parse(
    //   window.sessionStorage.getItem('realTimeLineSpotIdCopy')
    // )

    // 如果当前环境是开发环境才调用登录方法
    if (process.env.NODE_ENV == 'development') {
      this.login()
    }

    // this.initTableData()
    this.getValidateForm()
    this.getAllSpotList()
  },
  mounted() {
    // // 监听当前页面是不是从柱点过来的
    // const listenSpot = () => {
    //   this.timer = setInterval(() => {
    //     console.log('监听')
    //     var realTimeLineSpotId = JSON.parse(
    //       window.sessionStorage.getItem('realTimeLineSpotId')
    //     )

    //     if (realTimeLineSpotId) {
    //       // 设置一个新的缓存
    //       window.sessionStorage.setItem(
    //         'realTimeLineSpotIdCopy',
    //         realTimeLineSpotId
    //       )
    //       // 删除旧缓存
    //       window.sessionStorage.removeItem('realTimeLineSpotId')
    //       window.location.reload()
    //     }
    //   }, 1000)
    // }
    // listenSpot()

    // // 监听页面打开状态
    // document.addEventListener('visibilitychange', () => {
    //   const state = document.visibilityState
    //   // console.log(state) // hidden 表示当前页面切换为隐藏状态，visible表示当前页面切换为浏览状态
    //   if (state == 'visible') {
    //     listenSpot()
    //   } else {
    //     clearInterval(this.timer)
    //   }
    // })
    // this.drawEcharts()
    this.getRevenueLine()
	this.randomDate()
  },
  beforeDestroy() {
    clearInterval(this.timer)
  },
  methods: {
    // 获取表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.validateForm = validateForm
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=houjingchen&password=houjingchen'
      )
	  console.log(res)
	  // if(res.data)
    },

    /**
     * 获取景区列表
     */
    async getAllSpotList() {
      const { data: res } = await this.$http.get(
        '/system/robot_time_statistics/spotList'
      )
      this.searchScenicOptions = res.data
    },

    /**
     * 获取当日营收曲线数据
     */
    async getRevenueLine() {
		console.log(this.searchValidateForm)
      // this.drawEcharts()
		  var spotId = this.searchValidateForm.spotId
		  var startEndDate = this.searchValidateForm.startEndDate
		  var startDate = null
		  var endDate = null
		  var dateType = 1
		if(this.dateType == 'daterange'){
			startDate = formatDateTime(startEndDate[0],1)
			endDate = formatDateTime(startEndDate[1],1)
			dateType = 1
		}else if(this.dateType == 'monthrange'){
			startDate = formatDateTime(startEndDate[0],2)
			endDate = formatDateTime(startEndDate[1],2)
			dateType = 2
		}else if(this.dateType == 'yearrange'){
			startDate = formatDateTime(startEndDate[0],3)
			endDate = formatDateTime(startEndDate[1],3)
			dateType = 3
		}
	console.log({
          spotId,
		  dateType,
          startTime: startDate,
          endTime: endDate,
        })
      // 加载Echarts loading效果
      // this.myChart.showLoading({
      //   text: 'loading',
      //   color: '#c23531',
      //   textColor: '#000',
      //   maskColor: 'rgba(255, 255, 255, 0.2)',
      //   zlevel: 0
      // })
		var params = {}
		if(spotId == ''){
			if(this.dateType == 'daterange'){
				params = {
				  spotId,
				  startDate: startDate,
				  endDate: endDate,
				}
			}else if(this.dateType == 'monthrange'){
				params = {
				  spotId,
				  dateType:4,
				  startDate: startDate,
				  endDate: endDate,
				}
			}else if(this.dateType == 'yearrange'){
				params = {
				  spotId,
				  dateType:5,
				  startDate: startDate,
				  endDate: endDate,
				}
			}
		}else{
			params = {
			  spotId,
			  dateType,
			  startDate: startDate,
			  endDate: endDate,
			}
		}
      const { data: res } = await this.$http.get('/system/robot_time_statistics/amountByTime', {
        params:params
      })

      if (res.state == '200') {
		  console.log(res)
		  var date = []
		  var datas = []
		  res.data.forEach(item => {
			  if(this.dateType == 'daterange'){
				  date.push(item.DATE)
				  datas.push(item.total_amount)
			  }else if(this.dateType == 'monthrange'){
				  date.push(item.month)
				  datas.push(item.total_amount)
			  }else if(this.dateType == 'yearrange'){
				   date.push(item.year)
				   datas.push(item.total_amount)
			  }
		  })
		  console.log(date)
		  this.myChart = this.$echarts.init(document.getElementById('myChart'))
		  this.option = {
			   title: {
			      text: '统计总金额'
			    },
			  tooltip: {
			    trigger: 'axis',
			    axisPointer: {
			      type: 'cross',
			      crossStyle: {
			        color: '#999'
			      }
			    },
			    textStyle: {
			      align: 'left' // 文字居左
			    },
				formatter:function(params){
					return `日期：${params[0].name}</br>统计营收:${params[0].value}元`
				}
			  },
			    grid: {
			      left: '3%',
			      right: '4%',
			      bottom: '3%',
			      containLabel: true
			    },
			    xAxis: {
			      type: 'category',
			      data: [...new Map(date.map(item => [item, item]))].map(item => item[1])
			    },
			    yAxis: {
					type: 'value',
			    },
			    series: [
			      {
			        data: datas,
			        type: 'line'
			      }
			    ]
		  }
        this.myChart.setOption(this.option)
      } else {
        this.$message({
          type: 'error',
          message: '无数据',
          offset: 100,
          center: true,
          duration: 3000
        })
        // 隐藏echart的loadnig效果
        // this.myChart.hideLoading()
      }
    },
    // 点击搜索按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.getRevenueLine()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
	
	//获取随机日期
	randomDate(index){
		  let date = new Date(); // 获取当前日期  
		  let previous7Days = [];  
		  for (let i = 1; i <= index; i++) {  
		    date.setDate(date.getDate() - 1); // 将日期设置为前i天 
			var dateType = new Date(date.getTime())
			var year = dateType.getFullYear().toString();
			var month = (dateType.getMonth() + 1).toString().padStart(2, "0");
			var date1 = dateType.getDate().toString().padStart(2, "0");
			var sevenDate = year + "-" + month + "-" + date1;
		    previous7Days.push(sevenDate); // 将前i天的日期添加到数组中  
		  }  
		console.log(previous7Days)
		return previous7Days;  
	},
	
    // 点击刷新按钮时触发
    onRefresh() {
      this.searchValidateForm.spotIdList = []
      this.searchValidateForm.spotId = ''
      this.searchValidateForm.mode = 0
      this.searchValidateForm.date = new Date()
      this.searchValidateForm.startEndDate = [new Date(), new Date()]
      this.searchValidateForm.multipeDate = [new Date()]

      // 更新单选景区多选日期的景区下拉选
      this.updateSelectSpotIdList = false
      this.$nextTick(() => {
        this.updateSelectSpotIdList = true
      })

      // 更新单选景区多选日期的日期下拉选
      this.updateSelectDate = false
      this.$nextTick(() => {
        this.updateSelectDate = true
      })

      // 更新单选景区多选时间范围的景区下拉选
      this.updateSelectSpotId = false
      this.$nextTick(() => {
        this.updateSelectSpotId = true
      })

      this.getRevenueLine()
    },

    /**
     * 画echart图表
     */
    drawEcharts() {
      // document.getElementById('myChart').removeAttribute('_echarts_instance_')
      this.myChart = this.$echarts.init(document.getElementById('myChart'))

      this.option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          },
          textStyle: {
            align: 'left' // 文字居左
          }
        },
        toolbox: {
          feature: {
            dataView: { show: true, readOnly: false },
            magicType: { show: true, type: ['line', 'bar'] },
            // restore: { show: true },
            saveAsImage: { show: true }
          }
        },
        legend: {
          bottom: 0,
          data: ['统计营收/元', '统计订单/条']
        },
        xAxis: [
          {
            type: 'category',
            data: [],
            axisPointer: {
              type: 'shadow'
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '统计',
            axisLabel: {
              formatter: '{value} 元'
            },
            max: null, // 最大订单数(null/undefined 来表示“自动计算最大值”)
            splitNumber: 6, // 刻度数
            interval: null // 限制分隔间隔
          },
          // {
          //   type: 'value',
          //   name: '在线订单',
          //   axisLabel: {
          //     formatter: '{value} 条'
          //   },
          //   max: null, // 最大订单数(null/undefined 来表示“自动计算最大值”)
          //   splitNumber: 6, // 刻度数
          //   interval: null // 限制分隔间隔
          // }
        ],
        series: []
      }

      // 初始化series列表（解决渲染接口数据时yAxisIndex无效的问题）
      for (let i = 0; i < 999; i++) {
        if (i % 2 == 0) {
          this.option.series[i] = {
            type: 'line',
            yAxisIndex: 0
          }
        } else {
          this.option.series[i] = {
            type: 'line',
            yAxisIndex: 1
          }
        }
      }

      this.myChart.setOption(this.option, true)

      // 图表自适应
      window.addEventListener('resize', () => {
        this.myChart.resize()
      })
    },

    // 搜索时模式改变时触发
    onChangeMode(val) {
		// dateType: 'daterange', // 日期类型（月，日，年）
		// valueFormat: 'yyyy-MM-dd', // 日期格式
      if (val == 1) {
		  this.dateType = 'daterange'
		  // this.valueFormat = 'yyyy-MM-dd'
      } else if (val == 2) {
		  this.dateType = 'monthrange'
		  // this.valueFormat = 'yyyy-MM'
      } else if (val == 3) {
         this.dateType = 'yearrange'
         // this.valueFormat = 'yyyy'
      }
    },
  },
  components: {
    // DownloadExcel: JsonExcel,
  }
}
</script>

<style lang="scss" scoped>
