  display: flex;
  justify-content: center;
  align-items: center;
  background: #f2f2f2;
  margin-bottom: 10px;
  height: 80px !important;
}
.header > * {
  /**设置header下的所有子元素样式 */
  // margin: 0 10px;
  display: flex;
}
.header .input-name {
  width: 150px;
}

.header .el-form-item {
  margin-bottom: 0;
}

.el-header,
.el-main {
  padding: 0;
}

.my-echarts {
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

#myChart {
  width: 95vw;
  height: 80vh;
}
</style>
